import { Component, VERSION } from '@angular/core'
import { BrowserModule } from '@angular/platform-browser';

import {
    Action, AdvancedLayout, ButtonEvent, ButtonsConfig, ButtonsStrategy, ButtonType, Description, DescriptionStrategy,
    DotsConfig, GridLayout, Image, ImageModalEvent, LineLayout, PlainGalleryConfig, PlainGalleryStrategy, PreviewConfig
} from 'angular-modal-gallery';


@Component({
    selector: 'glen-gallery',
    templateUrl: 'gallery.component.html'
})

export class GlenGallery {
    
      plainGalleryRow: PlainGalleryConfig = {
        strategy: PlainGalleryStrategy.ROW,
        layout: new LineLayout({ width: '200px', height: '170px' }, { length: 18, wrap: true }, 'flex-start')
      };
    
      images: Image[] = [
        new Image(0,{img: 'assets/images/gallery/1a.jpg'}),
        new Image(1,{img: 'assets/images/gallery/2a.jpg',description: 'Image 2'}),      
        new Image(2,{img: 'assets/images/gallery/3a.jpg',description: 'Image 3'}),      
        new Image(3,{img: 'assets/images/gallery/4a.jpg',description: 'Image 4'}),      
        new Image(4,{img: 'assets/images/gallery/5a.jpg',description: 'Image 5'}),      
        new Image(5,{img: 'assets/images/gallery/6a.jpg',description: 'Image 6'}),      
        new Image(6,{img: 'assets/images/gallery/7a.jpg',description: 'Image 7'}),      
        new Image(7,{img: 'assets/images/gallery/8a.jpg',description: 'Image 8'}),      
        new Image(8,{img: 'assets/images/gallery/9a.jpg',description: 'Image 9'}),      
        new Image(9,{img: 'assets/images/gallery/10a.jpg',description: 'Image 10'}),      
        new Image(10,{img: 'assets/images/gallery/11a.jpg',description: 'Image 11'}),      
        new Image(11,{img: 'assets/images/gallery/11aa.jpg',description: 'Image 12'}),      
        new Image(12,{img: 'assets/images/gallery/15.jpg',description: 'Image 13'}),      
        new Image(13,{img: 'assets/images/gallery/16.jpg',description: 'Image 14'}),      
        new Image(14,{img: 'assets/images/gallery/17.jpg',description: 'Image 15'}),      
        new Image(15,{img: 'assets/images/gallery/18.jpg',description: 'Image 16'}),      
        new Image(16,{img: 'assets/images/gallery/12a.jpg',description: 'Image 17'}),      
        new Image(17,{img: 'assets/images/gallery/13a.jpg',description: 'Image 18'})    
      ];
    
    
      // array with a single image inside (the first one)
      singleImage: Image[] = [this.images[0]];
    
      dotsConfig: DotsConfig = {
        visible: false
      };
    
      previewConfig: PreviewConfig = {
        visible: false,
        number: 3,
        arrows: true,
        clickable: true,
        size: {
          width: '70px',
          height: '70px'
        }
      };
    
    
    }


