import { Component } from '@angular/core';
import { TariffDataService } from '../data-service/data-tariff.service';

@Component({
    selector: 'glen-tariff',
    templateUrl: 'tariff.component.html'
})

export class GlenTariff {
    tariff: Array<any>
    constructor(private _TariffDataSerive: TariffDataService) {
        this._TariffDataSerive.getTariff()
            .subscribe(res => this.tariff = res);
    }
}


