import { Routes } from '@angular/router';

import { GlenHomePage } from './app/home/home.component';
import { GlenRoomService } from './app/services/roomservice/room.component';
import { GlenCateringService } from './app/services/cateringservice/catering.component'
import { GlenTouristService } from './app/services/touristservice/tourist.component';
import { GlenCottage } from './app/cottages/cottage.component';
import { GlenTariff } from './app/tariff/tariff.component';
import { GlenContact } from './app/contact/contact.component';
import { GlenGallery } from './app/gallery/gallery.component';
import { GlenAbout } from './app/about/about.component';
import { GlenDestination } from './app/destination/destination.component';
import { GlenTestimonial } from './app/testimonial/testimonial.component';

export const appRoutes:Routes = [
    { path: '', component: GlenHomePage },
    { path: 'room-services', component: GlenRoomService },
    { path: 'catering-services', component: GlenCateringService },
    { path: 'tourist-services', component: GlenTouristService },
    { path: 'cottages', component: GlenCottage },
    { path: 'tariff', component: GlenTariff },
    { path: 'contact', component: GlenContact },
    { path: 'gallery', component: GlenGallery },
    { path: 'about', component: GlenAbout },
    { path: 'destination', component: GlenDestination },
    { path: 'testimonial', component: GlenTestimonial },
    { path: '',  redirectTo: '/', pathMatch: 'full'}
]


