import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HttpModule } from '@angular/http';
import 'hammerjs';
import 'mousetrap';
import { ModalGalleryModule } from 'angular-modal-gallery'; 

// Data Service
import { TariffDataService } from './data-service/data-tariff.service';

import { AppComponent } from './app.component';
import { GlenHeader } from './header/header.component';
import { GlenHomePage } from './home/home.component'; 
import { GlenSlider } from './home/slider/slider.component'; 
import { GlenRooms } from './home/rooms/rooms.component';
import { GlenRoomService } from './services/roomservice/room.component';
import { GlenCateringService } from './services/cateringservice/catering.component';
import { GlenTouristService } from './services/touristservice/tourist.component';
import { GlenCottage } from './cottages/cottage.component';
import { GlenTariff } from './tariff/tariff.component';
import { GlenContact } from './contact/contact.component';
import { GlenGallery } from './gallery/gallery.component';
import { GlenAbout } from './about/about.component';
import { GlenDestination } from './destination/destination.component';
import { GlenTestimonial } from './testimonial/testimonial.component';
import { CommonServiceComponent } from './common-service/commonRoomService.component';
import { FooterGlenComponent } from './footer/footer.component';

import { appRoutes } from '../routes';


@NgModule({
  declarations: [
    AppComponent,
    GlenHeader,
    GlenHomePage,
    GlenSlider,
    GlenRooms,
    GlenRoomService,
    GlenCateringService,
    GlenTouristService,
    GlenCottage,
    GlenTariff,
    GlenContact,
    GlenGallery,
    GlenAbout,
    GlenDestination,
    GlenTestimonial,
    CommonServiceComponent,
    FooterGlenComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes, { useHash: true }),
    ModalGalleryModule.forRoot(),
    HttpModule
  ],
  providers: [TariffDataService],
  bootstrap: [AppComponent]
})
export class AppModule { }



